@use "../../variables/variables" as *;

.build {
  display: flex;
  align-items: center;
  gap: 129px;
  @media (max-width: 1240px) {
    gap: 86px;
  }
  @media (max-width: 1025px) {
    gap: 64px;
  
  }
  @media (max-width: 992.99px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 48px;
  }
}
.build__container {
  padding-inline-end: 82px;
  @media (max-width: 1025px) {
  
  }
  @media (max-width: 768px) {
      padding-inline-end: 30px;
  
  }
}

.build__column {
  @media (max-width: 1025px) {
    object-fit: cover;
  }
}
.build__title {
  @include font($primaryTitle);
  padding-block-end: 16px;
  @media (max-width: 1025px) {
    max-width: 380px;
  
  }

  @media (max-width: 992.99px) {
    padding-block-start: 0px;
    text-align: center;
  }
  @media (max-width: 768px) {
    font-size: 32px;
    line-height: 40px;
    text-align: start;
    max-width: 280px;
    letter-spacing: -0.3px;
  }
}
.build__column--title {
  max-width: 531px;
  @media (max-width: 1025px) {
  }
}
.build__text {
  @include font($primaryText);
  color: $secondaryColor;
}
.build__text--paddding {
  padding-block-start: 16px;
  @media (max-width: 1025px) {
  }
  @media (max-width: 992.99px) {
    padding-block-end: 0px;
  }
}
.build__column-img {
  object-fit: cover;
  border-radius: 8px;
  @media (max-width: 1280px) {
    height: 500px;
    min-width: 400px;
  }

  @media (max-width: 768px) {
    height: 400px;
    min-width: 300px;
  }
}
