.build {
  display: flex;
  align-items: center;
  gap: 129px;
}
@media (max-width: 1240px) {
  .build {
    gap: 86px;
  }
}
@media (max-width: 1025px) {
  .build {
    gap: 64px;
  }
}
@media (max-width: 992.99px) {
  .build {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 48px;
  }
}

.build__container {
  padding-inline-end: 82px;
}
@media (max-width: 768px) {
  .build__container {
    padding-inline-end: 30px;
  }
}

@media (max-width: 1025px) {
  .build__column {
    object-fit: cover;
  }
}

.build__title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1px;
  padding-block-end: 16px;
}
@media (max-width: 1025px) {
  .build__title {
    max-width: 380px;
  }
}
@media (max-width: 992.99px) {
  .build__title {
    padding-block-start: 0px;
    text-align: center;
  }
}
@media (max-width: 768px) {
  .build__title {
    font-size: 32px;
    line-height: 40px;
    text-align: start;
    max-width: 280px;
    letter-spacing: -0.3px;
  }
}

.build__column--title {
  max-width: 531px;
}
.build__text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}

.build__text--paddding {
  padding-block-start: 16px;
}
@media (max-width: 992.99px) {
  .build__text--paddding {
    padding-block-end: 0px;
  }
}

.build__column-img {
  object-fit: cover;
  border-radius: 8px;
}
@media (max-width: 1280px) {
  .build__column-img {
    height: 500px;
    min-width: 400px;
  }
}
@media (max-width: 768px) {
  .build__column-img {
    height: 400px;
    min-width: 300px;
  }
}